import ProductInfo from "./product-info";
import { useState,useMemo } from "react";
import { getFirestore,doc, getDoc } from "firebase/firestore";
import  '../../../data';
import ProductInfoLoader from "./product-info-loader";
import { useParams } from "react-router-dom";

const Product = () => {
    const {id} = useParams();
    const [lazyblock,setLazyBlock] = useState('');
    const [ProductItem,setProductItem] = useState([
        {id:1,images:process.env.PUBLIC_URL+'/images/factory.svg',title:'–ü—Ä–æ–∏–∑–≤–æ–¥–∏—Ç–µ–ª—å',content:"–ó–∞–≥—Ä—É–∂–∞–µ–º –¥–∞–Ω–Ω—ã–µ. –ü–æ–∂–∞–ª—É–π—Å—Ç–∞ –ø–æ–¥–æ–∂–¥–∏—Ç–µ."},
        {id:2,images:process.env.PUBLIC_URL+'/images/security.svg',title:'–§–∞—Ä–º–∞–∫–æ—Ç–µ—Ä–∞–ø–µ–≤—Ç–∏—á–µ—Å–∫–∞—è –≥—Ä—É–ø–ø–∞',content:"–ó–∞–≥—Ä—É–∂–∞–µ–º –¥–∞–Ω–Ω—ã–µ. –ü–æ–∂–∞–ª—É–π—Å—Ç–∞ –ø–æ–¥–æ–∂–¥–∏—Ç–µ."},
        {id:3,images:process.env.PUBLIC_URL+'/images/forads.svg',title:'–î–µ–π—Å—Ç–≤—É—é—â–µ–µ –≤–µ—â–µ—Å—Ç–≤–æ:',content:"–ó–∞–≥—Ä—É–∂–∞–µ–º –¥–∞–Ω–Ω—ã–µ. –ü–æ–∂–∞–ª—É–π—Å—Ç–∞ –ø–æ–¥–æ–∂–¥–∏—Ç–µ."},
        {id:4,images:process.env.PUBLIC_URL+'/images/lekform.svg',title:'–õ–µ–∫–∞—Ä—Å—Ç–≤–µ–Ω–Ω–∞—è —Ñ–æ—Ä–º–∞:',content:"–ó–∞–≥—Ä—É–∂–∞–µ–º –¥–∞–Ω–Ω—ã–µ. –ü–æ–∂–∞–ª—É–π—Å—Ç–∞ –ø–æ–¥–æ–∂–¥–∏—Ç–µ."}
    ]);
    useMemo(()=>{
        setLazyBlock(false);
        let check = true;
        const db = getFirestore();
        getDoc(doc(db,'list',id)).then((docSnap)=>{
          if(docSnap.exists()) {
            const s = docSnap.data();
            if(check) { 
                setProductItem([
                    {id:1,images:process.env.PUBLIC_URL+'/images/factory.svg',title:'–ü—Ä–æ–∏–∑–≤–æ–¥–∏—Ç–µ–ª—å',content:s.production},
                    {id:2,images:process.env.PUBLIC_URL+'/images/security.svg',title:'–§–∞—Ä–º–∞–∫–æ—Ç–µ—Ä–∞–ø–µ–≤—Ç–∏—á–µ—Å–∫–∞—è –≥—Ä—É–ø–ø–∞',content:s.pharm_group},
                    {id:3,images:process.env.PUBLIC_URL+'/images/forads.svg',title:'–î–µ–π—Å—Ç–≤—É—é—â–µ–µ –≤–µ—â–µ—Å—Ç–≤–æ:',content:s.active},
                    {id:4,images:process.env.PUBLIC_URL+'/images/lekform.svg',title:'–õ–µ–∫–∞—Ä—Å—Ç–≤–µ–Ω–Ω–∞—è —Ñ–æ—Ä–º–∞:',content:s.form}
                ]);
                setLazyBlock(true);
            }
          }
          else console.log("error. Don't exist!");
        });
        console.log('updated');
        return () => {
          check=false;
        };
      },[id]);
    return(
        <div>
        
        {lazyblock ? ProductItem.map(product=>{
            return(<ProductInfo item={product} key={product.id} />);
        }):<ProductInfoLoader/>}
        </div>
    );
};

export default Product;